export default {
	props: {
		list: {
			type: Array,
			default: () => ['请选择']
		},
		initIndex: {
			type: Number,
			default: 0
		}
	},

	data() {
		return {
			index: this.initIndex
		};
	},

	methods: {
		change(e) {
			const { value: index } = e.detail;

			this.index = index;
			this.$emit('change', this.list[index], index);
		}
	}
};
